
.client-list {
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	width: 40%;
  color: blue;
  padding-left: 2rem;
}

.client-name {
	height: 20%;
	display: flex;
	align-items: center;
	font-size: 2rem;
  text-align: left;
  text-transform: uppercase;
}



.client-name:hover{
  cursor: pointer;
  text-decoration: underline;
}

@media only screen and (max-width: 750px) {
	
.client-list {
  padding-left: 2rem;
}
}